@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600&display=swap);
:root {
  --orange: #ff7800;
  --black: #130f40;
  --light-color: #666;
  --box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.1);
  --border: 0.2rem solid rgba(0, 0, 0, 0.1);
  --outline: 0.1rem solid rgba(0, 0, 0, 0.1);
  --outline-hover: 0.2rem solid var(--black);
}
* {
  text-transform: capitalize;
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  text-decoration: none;
  border: none;
  transition: all linear 0.2s;
}
html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-padding-top: 7rem;
}
.btn {
  margin-top: 1rem;
  display: inline-block;
  padding: 0.8rem 3rem;
  font-size: 1.7rem;
  color: #130f40;
  color: var(--black);
  border: 0.2rem solid #130f40;
  border: 0.2rem solid var(--black);
  border-radius: 0.5rem;
  cursor: pointer;
  background: none;
}
.btn:hover {
  color: #fff;
  background: #ff7800;
  background: var(--orange);
}
section,
footer {
  padding: 2rem 9%;
}
body {
  background: #eee;
}
.heading {
  text-align: center;
  font-size: 3.5rem;
  color: #130f40;
  color: var(--black);
  padding: 2rem 0 3rem 0;
}
.heading span {
  background: #ff7800;
  background: var(--orange);
  color: #fff;
  display: inline-block;
  padding: 0.5rem 3rem;
  -webkit-clip-path: polygon(100% 0, 93% 50%, 100% 99%, 0 100%, 7% 50%, 0 0);
          clip-path: polygon(100% 0, 93% 50%, 100% 99%, 0 100%, 7% 50%, 0 0);
}

@media (max-width: 991px) {
  html {
    font-size: 55%;
  }

  .header {
    padding: 2rem!important;
  }

  section {
    padding: 2rem;
  }
}
@media (max-width:450px){

  html{
      font-size: 50%;
  }

  .heading{
      font-size: 2.5rem;
  }

  .footer{
      text-align: center;
  }

  .footer .box-container .box img{
      margin: 2rem auto;
  }

}
.header .search-form {
  position: absolute;
  top: 110%;
  right: -110%;
  height: 5rem;
  width: 50rem;
  background: #fff;
  border-radius: 0.5rem;
  box-shadow: var(--box-shadow);
}
.header .search-form.active {
  right: 2rem;
}
.header .search-form #search-box {
  height: 100%;
  width: 100%;
  text-transform: none;
  padding: 0 1.5rem;
  font-size: 1.6rem;
  border-radius: 0.5rem;
}
.header .search-form label {
  display: flex;
  align-items: center;
  height: inherit;
  overflow: hidden;
}
.header .search-form .search-icon {
  font-size: 3.5rem;
  padding-right: 1.5rem;
  color: var(--black);
  cursor: pointer;
  font-weight: bold;
}
.header .search-form .search-icon:hover {
  color: var(--orange);
}
@media (max-width: 768px) {
  .header .search-form {
    width: 90%;
  }
}

.header .shopping-cart {
  position: absolute;
  top: 110%;
  right: -110%;
  padding: 1rem;
  border-radius: 0.5rem;
  width: 30rem;
  box-shadow: var(--box-shadow);
  background: #fff;
}
.header .shopping-cart.active {
  right: 2rem;
}

.header .shopping-cart .box {
  display: flex;
  grid-gap: 1;
  gap: 1;
  align-items: center;
  margin: 1rem 0;
  position: relative;
}
.header .shopping-cart .box img {
  height: 10rem;
}
.header .shopping-cart .box .content h3 {
  color: var(--black);
  font-size: 1.9rem;
}
.header .shopping-cart .box .content span {
  color: var(--light-color);
  font-size: 1.6rem;
}
.header .shopping-cart .box .content .quantity {
  margin-left: 1rem;
}
.header .shopping-cart .box .trash-icon {
  position: absolute;
  top: 50%;
  right: 1.2rem;
  color: var(--light-color);
  font-size: 2rem;
  cursor: pointer;
  transform: translateY(-50%);
}

.header .user-form {
  position: absolute;
  top: 110%;
  right: -110%;
  width: 35rem;
  border-radius: 0.5rem;
  text-align: center;
  padding: 2rem;
  background: #fff;

}
.header .user-form.active {
  right: 2rem;
}
.header .user-form h3 {
  text-transform: uppercase;
  font-size: 2.5rem;
  color: var(--black);
}
.header .user-form .box input {
  width: 100%;
  margin: 1rem 0;
  font-size: 1.6rem;
  text-transform: none;
  color: var(--black);
  padding: 1rem;
  background: #eee;
  border-radius: 0.5rem;
}
.header .user-form p {
  font-size: 1.4rem;
  color: var(--light-color);
}
.header .user-form p a {
  color: var(--orange);
  text-decoration: underline;
}
.header .user-form button:hover {
  background: var(--orange);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 9%;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 9999;
  background: #fff;
  box-shadow: var(--box-shadow);
}
.header .logo {
  font-size: 2.5rem;
  font-weight: bold;
  color: var(--black);
}
.header .logo i {
  color: var(--orange);
  margin-right: 0.5rem;
}
.header .icons button {
  height: 4.5rem;
  width: 4.5rem;
  line-height: 4.5rem;
  margin-left: 0.5rem;
  cursor: pointer;
  text-align: center;
  font-size: 2rem;
  background: #eee;
  border-radius: 0.5rem;
}
.header .icons button .fa-icon {
  color: var(--black);
  transition-duration: 0s;
}
.header .icons button:hover .fa-icon {
  color: #fff;
}
.header .icons button:hover {
  background: var(--orange);
}
.header .icons #menu-btn {
  display: none;
}

.header .navbar a {
  display: inline-block;
  color: var(--black);
  font-size: 1.7rem;
  margin: 0 1rem;
}
.header .navbar.active {
  right: 2rem;
}
.header .navbar a:hover {
  color: var(--orange);
}

@media (max-width: 768px) {
  .header {
    position: relative;
  }
  .header .icons #menu-btn {
    display: inline-block;
  }
  .header .navbar {
    position: absolute;
    top: 110%;
    right: -110%;
    width: 30rem;
    background: #fff;
    border-radius: 0.5rem;
  }
  .header .navbar a {
    display: block;
    font-size: 2rem;
    margin: 2rem 2.5rem;
  }
}

.banner {
  padding-top: 17rem;
  padding-bottom: 10rem;
  display: flex;
  justify-content: center;
}
.banner .content {
  text-align: center;
  width: 60rem;
}
.banner .content h3 {
  font-size: 3rem;
  color: var(--black);
}
.banner .content span {
  color: var(--orange);
}
.banner .content p {
  padding: 2rem 0;
  font-size: 1.7rem;
  color: var(--light-color);
}
.banner .content button:hover {
  background: var(--orange);
}

.features .heading {
  text-align: center;
  font-size: 3.5rem;
  color: var(--black);
  padding: 2rem 0 3rem 0;
}
.features span {
  background: var(--orange);
  color: #fff;
  display: inline-block;
  padding: 0.5rem 3rem;
  -webkit-clip-path: polygon(100% 0, 93% 50%, 100% 99%, 0 100%, 7% 50%, 0 0);
          clip-path: polygon(100% 0, 93% 50%, 100% 99%, 0 100%, 7% 50%, 0 0);
}
.features .content .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  grid-gap: 1.5rem;
  gap: 1.5rem;
  flex-wrap: wrap;
}
.features .content .box-container .box {
  text-align: center;
  box-shadow: var(--box-shadow);
  outline: var(--outline);
  outline-offset: -1rem;
  padding: 3rem 2rem;
  transition: linear 0.2s;
  background: #fff;
}
.features .content .box-container .box:hover {
  outline-offset: 0;
  outline: var(--outline-hover);
}
.features .content .box-container .box img {
  height: 15rem;
  margin: 1rem 0;
}
.features .content .box-container .box h3 {
  font-size: 2.5rem;
  line-height: 1.8;
  color: var(--black);
}
.features .content .box-container .box p {
  font-size: 1.5rem;
  line-height: 1.8;
  color: var(--light-color);
  padding: 1rem 0;
}
.features .content .box-container .box a:hover {
  background: var(--orange);
}

.products .products-slider .wrapper .box {
  background: #fff;
  border-radius: 0.5rem;
  padding: 3rem 2rem;
  text-align: center;
  outline-offset: -1rem;
  outline: var(--outline);
  box-shadow: var(--box-shadow);
}
.products .products-slider .box:hover {
  outline-offset: 0;
  outline: var(--outline-hover);
}
.products .products-slider .box img {
  height: 20rem;
}
.products .products-slider .box h3 {
  font-size: 2.5rem;
  color: var(--black);
}
.products .products-slider .box .price {
  font-size: 2rem;
  color: var(--light-color);
}
.products .products-slider .box .stars {
  font-size: 1.7rem;
  padding: 0.5rem 0;
  color: var(--orange);
}

.categories .box-container{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(26rem, 1fr));
  grid-gap: 1.5rem;
  gap: 1.5rem
}
.categories .box{
  background: #fff;
  box-shadow: var(--box-shadow);
  border-radius: 0.5rem;
  padding: 3rem 2rem;
  text-align: center;
  outline: var(--outline);
  outline-offset: -1rem;
}
.categories .box:hover{
  outline-offset: 0;
  outline: var(--outline-hover);
}
.categories .box img{
  height: 15rem;
  margin: 1rem 0;
}
.categories .box h3{
  font-size:2rem;
  line-height:1.8;
  color: var(--black);
}
.categories .box p{
  color: var(--light-color);
  list-style: 1.8;
  font-size: 1.7rem;
  padding: 1rem 0;
}

.reviews .reviews-slider .box{
  background: #fff;
  border-radius: 0.5rem;
  text-align: center;
  padding: 3rem 2rem;
  outline: var(--outline);
  outline-offset: -1rem;
  box-shadow: var(--box-shadow);
}
.reviews .reviews-slider .box:hover{
  outline-offset: 0;
  outline: var(--outline-hover);
}
.reviews .reviews-slider .box img{
  height: 10rem;
  width: 10rem;
  border-radius: 50%;
}
.reviews .reviews-slider .box p{
  padding: 1rem 0;
  line-height:1.8;
  font-size: 1.5rem;
  color: var(--light-color);
}
.reviews .reviews-slider .box h3{
  padding: 0.5rem;
  color: var(--black);
  font-size: 2.2rem;
}
.reviews .reviews-slider .box .stars{
  color: var(--orange);
  font-size: 1.7rem;
}
.blogs .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(26rem, 1fr));
  grid-gap: 1.5rem;
  gap: 1.5rem;
}
.blogs .box-container .box {
  border-radius: 0.5rem;
  overflow: hidden;
  box-shadow: var(--box-shadow);
  background: #fff;
}
.blogs .box-container .box img {
  object-fit: cover;
  height: 25em;
  width: 100%;
}
.blogs .box-container .box .content {
  padding: 2rem;
}
.blogs .box-container .box .content .icons {
  display: flex;
  justify-content: space-between;
  border-bottom: var(--border);
  padding-bottom: 1.5rem;
  margin-bottom: 1rem;
}
.blogs .box-container .box .content .icons a {
  color: var(--light-color);
  font-size: 1.5rem;
}
.blogs .box-container .box .content .icons a:hover {
  color: var(--black);
}
.blogs .box-container .box .content .icons i {
  color: var(--orange);
  margin-right: 0.5rem;
}
.blogs .box-container .box .content h3 {
  color: var(--black);
  font-size: 2.2rem;
  line-height: 1.8;
  padding: 0.5rem 0;
}
.blogs .box-container .box .content p {
  color: var(--light-color);
  font-size: 1.5rem;
  line-height: 1.8;
  padding: 0.5rem 0;
}

.footer {
  background: #fff;
}
.footer .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(26rem, 1fr));
}
.footer .box-container h3 {
  color: var(--black);
  font-size: 2.5rem;
  padding: 1rem 0;
}
.footer .box-container h3 i {
  margin-left: 1rem;
  color: var(--orange);
}
.footer .box-container p {
  line-height: 1.8;
  font-size: 1.5rem;
  color: var(--light-color);
  margin: 1rem 0;
}
.footer .box-container .share a {
  height: 4rem;
  width: 4rem;
  margin-right: 0.4rem;
}
.footer .box-container .share a i {
  border-radius: 0.5rem;
  background: #eee;
  text-align: center;
  font-size: 2rem;
  line-height: 4rem;
  height: inherit;
  width: inherit;
  display: inline-block;
}
.footer .box-container .share a i .fa-icon {
  transition-duration: 0s;
  color: var(--black);
}
.footer .box-container .share a:hover i .fa-icon {
  color: #fff;
}
.footer .box-container .share a:hover i {
  background: var(--orange);
}
.footer .box-container .links {
  display: block;
  font-size: 1.5rem;
  color: var(--light-color);
  padding: 1rem 0;
}
.footer .box-container #emailLink {
  text-transform: none;
}
.footer .box-container .links i {
  color: var(--orange);
  padding-right: 0.5rem;
}
.footer .box-container .links:hover i {
  padding-right: 2rem;
}
.footer .box-container input {
  width: 100%;
  margin: 0.7rem 0;
  padding: 1rem;
  font-size: 1.6rem;
  color: var(--black);
  text-transform: none;
  background: #eee;
  border-radius: 0.5rem;
}
.footer .box-container button:hover {
  background: var(--orange);
}
.footer .box-container img {
  margin-top: 2rem;
  height: 3rem;
  display: block;
}
.footer .credits {
  text-align: center;
  margin: 1rem 0;
  padding: 3rem 0;
  border-top: var(--border);
  font-size: 2rem;
  color: var(--black);
}
.footer .credits span {
  color: var(--orange);
}

.app {
  overflow-x: hidden;
}

